import React from 'react'
import Link from 'gatsby-link'

export default class Nav extends React.Component {
	render () {
		return (
			<nav>
				<span>Britton Stanfill</span>
				<div className="menu">
					<div>
						<Link to='/'>Home</Link>
					</div>
					<div>
						<Link to='blog'>Blog</Link>
					</div>
					<div>
						<Link to='courses'>Courses</Link>
					</div>
				</div>
			</nav>
		)
	}
}


